.wrapper-middle {
	height: 100%;
	display: grid;
	grid-template-columns: 80% 20%;
	grid-column-gap: 18%;
	margin: 1.5% 12% 3.85% 26%;
	// padding: 1.6% 0 3.3% 0;
	padding: 2% 0;
	h1.how-it-started {
		font-size: 2.6em;
		font-weight: bold;
		color: #ffd200;
		// margin-left: 0%;
		// margin-top: 0;
		margin-bottom: 4%;
	};
	p.thick {
		line-height: 1.5em;
		font-size: 0.9em;
		margin-bottom: 0;
		text-align: center;
	}
	.feedback {
		margin: auto;
		color: #ffd200;
		font-weight: bold;
		p {
			text-align: center;
		}
	}
}

.wrapper-middle-to-be-updated {
	height: 100%;
	display: grid;
	grid-template-columns: 80% 20%;
	grid-column-gap: 18%;
	margin: 1.5% 12% 3.85% 26%;
	// padding: 1.6% 0 3.3% 0;
	padding: 2% 0;
	h1.how-it-started {
		font-size: 2.6em;
		font-weight: bold;
		color: #ffd200;
		// margin-left: 0%;
		// margin-top: 0;
		margin-bottom: 4%;
	};
	p.thick {
		line-height: 1.5em;
		font-size: 0.9em;
		margin-bottom: 0;
		text-align: center;
	}
	.feedback {
		margin: auto;
		color: #ffd200;
		font-weight: bold;
		p {
			text-align: center;
		}
	}
}

.wrapper-down {
	height: 100%;
	display: grid;
	grid-template-columns: 25% 25% 25% 25%;
	/* column-gap: 3%; */
	background:	#666666;
	color: #fff;
	padding: 1.0% 13% 1.5% 26%;
	border-top: 7px solid #ffd200;
	/* margin-left: auto;
	margin-right: auto; */
	/* padding: 0% 0% 16% 28%; */
	/* margin-top: 15px; */
}

.wrapper-icons {
  display: grid;
	grid-template-columns: 50% 50%;
	/* grid-column-gap: 1%; */
	/* margin-left: -35%; */
}

p {
	font-size: 0.9em;
	text-align: left;
}

p.thin {
	line-height: 0.01em;
	font-size: 0.7em;
	.active {
		cursor: pointer;
		&:hover {
			color: #ffd200;
			text-decoration: underline;
		}
	}
}

p.thick {
	line-height: 1.4em;
	font-size: 0.9em;
}

p.thick-hidden {
	line-height: 0.39em;
	font-size: 0.9em;
	visibility: hidden;
}

p.thick-join-us {
	line-height: 2.0em;
	font-size: 0.7em;
}

.font-header {
	line-height: 2.5em;
	font-size: 1em;
	color: #ffd200;
	font-weight: bold;
	text-align: left;
}

.tooltip {
	max-width: 17.5%;
	font-weight: normal;
	line-height: 1.4em;

}

